<template>
	<view>
		<!--顶部选项卡-->
		<scroll-view scroll-x="true" class="scroll-row border-bottom border-white" :scroll-into-view="scrollInto" style="height: 100rpx;">
			<view
				v-for="(item, index) in tabBars"
				:key="index"
				class="scroll-row-item px-3 px-2 font-md font-weight-bold"
				:id="'tab' + index"
				:class="tabIndex === index ? 'text-main font-lg font-weight-bold' : ''"
				@click="changeTab(index)"
			>
				{{ item.name }}
			</view>
		</scroll-view>

		<swiper :duration="150" :current="tabIndex" @change="onChangeTab" :style="'height:' + scrollH + 'px;'">
			<swiper-item v-for="(item, index) in newsList" :key="index">
				<scroll-view scroll-y="true" :style="'height:' + scrollH + 'px;'" @scrolltolower="loadmore(index)">
					<template v-if="item.list.length">
						<!--列表-->
						<block v-for="(item2, index2) in item.list" :key="index2">
							<topicList :item="item2" :index="index2"></topicList>
						</block>
						<!--上拉加载-->
						<loadMore :loadmore="item.loadmore"></loadMore>
					</template>
					<template v-else>
						<nothing></nothing>
					</template>
				</scroll-view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
const demo = [
	{
		cover : "/static/bgimg/1.jpg",
		title : "话题名称",
		desc : "话题描述",
		today_count : 0,
		news_count : 10
	},{
		cover : "/static/bgimg/1.jpg",
		title : "话题名称",
		desc : "话题描述",
		today_count : 0,
		news_count : 10
	},
	{
		cover : "/static/bgimg/1.jpg",
		title : "话题名称",
		desc : "话题描述",
		today_count : 0,
		news_count : 10
	},
	{
		cover : "/static/bgimg/1.jpg",
		title : "话题名称",
		desc : "话题描述",
		today_count : 0,
		news_count : 10
	},
	{
		cover : "/static/bgimg/1.jpg",
		title : "话题名称",
		desc : "话题描述",
		today_count : 0,
		news_count : 10
	}
];

import topicList from '@/components/news/topic-list.vue'
import loadMore from '@/components/common/load_more.vue';
export default {
	components: {
		topicList,
		loadMore
	},
	data() {
		return {
			title: 'Hello',
			//列表高度
			scrollH: 600,
			//顶部选项卡
			scrollInto: '',
			tabIndex: 0,
			tabBars: [
				{
					name: '关注'
				},
				{
					name: '推荐'
				},
				{
					name: '体育'
				},
				{
					name: '热点'
				},
				{
					name: '财经'
				},
				{
					name: '娱乐'
				},
				{
					name: '军事'
				},
				{
					name: '历史'
				},
				{
					name: '本地'
				}
			],
			newsList: []
		};
	},
	// 监听导航栏
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search'
		});
	},
	// 监听导航按钮点击事件
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url:"../add-input/add-input"
		})
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - uni.upx2px(100);
			}
		});
		// 根据选项生成列表
		this.getData();
	},
	methods: {
		// 获取数据
		getData() {
			let arr = [];
			for (let i = 0; i < this.tabBars.length; i++) {
				// 生成列表数据
				let obj = {
					// 上拉加载更多
					loadmore: '上拉加载更多',
					list: []
				};
				if (i < 2) {
					obj.list = demo;
				}
				arr.push(obj);
			}
			this.newsList = arr;
		},
		follow(index) {
			this.list[index].isFollow = true;
			uni.showToast({
				title: '关注成功'
			});
		},
		doSupport(e) {
			let item = this.newsList[0].list[e.index]
			let msg = e.type === "support" ? "点赞" : "反对";
			if (item.support.type === '') {
				item.support.type = e.type;
				item.support[e.type + '_count']++;
			}
			if (item.support.type === 'support' && e.type === 'unsupport') {
				item.support.support_count--;
				item.support.unsupport_count++;
			} else if (item.support.type === 'unsupport' && e.type === 'support') {
				item.support.support_count++;
				item.support.unsupport_count--;
			}
			item.support.type = e.type;
			uni.showToast({
				title: msg + "成功"
			})
		},
		// 切换选项
		changeTab(index) {
			if (this.tabIndex === index) {
				return;
			} else {
				this.tabIndex = index;
				this.scrollInto = 'tab' + index;
			}
		},
		onChangeTab(e) {
			this.changeTab(e.detail.current);
		},
		// 上拉加载更多
		loadmore(index) {
			// 拿到当前列表
			let item = this.newsList[index];
			// 判断是否处于可加载状态（上拉加载更多）
			if (item.loadmore !== '上拉加载更多') {
				return;
			}
			// 修改当前列表加载状态
			this.newsList[index].loadmore = '加载中...';
			// 模拟数据请求
			setTimeout(() => {
				// 加载数据
				item.list = [...item.list, ...item.list];
				// 恢复加载状态
				this.newsList[index].loadmore = '上拉加载更多';
			}, 1000);
		}
	}
};
</script>

<style></style>
